import React, { useContext, useState } from 'react';
import { Link, useParams } from "react-router-dom";
import { PROFILE_ROUTE, HOME_ROUTE } from "../../utils/consts";
import { Context } from "../../index";
import { addAddress, editAddress, deleteAddress } from "../../http/userAPI";
import SideBar from "./components/menu";
const Address = () => {
    const { id, action, actionId } = useParams()
    const { user } = useContext(Context)
    const [update, setUpdate] = useState(1)
    const [address, setAddress] = useState((user.user.address && user.user.address.find(ids => ids.id == actionId)) ? user.user.address.find(ids => ids.id == actionId) : {})
    const deleteSubmit = async (id) => {
        try {
            let data;
            data = await deleteAddress(id);
            if (data) {
                user.setUser(data)
                setUpdate(update + 1)
            }
        } catch (e) {
            if (e.response && e.response.data) {
                alert(e.response.data.message)
            } else {
                alert(e)
            }
        }
    }
    if (action == 'add') {
        const submit = async (e) => {
            try {
                e.preventDefault();
                let data;
                data = await addAddress(address);
                if (data) {
                    setAddress({})
                    user.setUser(data)
                }
            } catch (e) {
                if (e.response && e.response.data) {
                    alert(e.response.data.message)
                } else {
                    alert(e)
                }
            }
        }
        const change = (e) => {
            setAddress({ ...address, [e.target.name]: e.target.value })
        }
        return (
            <main>
                <div className="container mb-4 mb-md-5">
                    <nav aria-label="breadcrumb">
                        <ol className="breadcrumb mb-0">
                            <li className="breadcrumb-item"><Link to={HOME_ROUTE}></Link></li>
                            <li className="breadcrumb-item"><a> </a></li>
                        </ol>
                    </nav>
                </div>
                <section id="sec-13" className="mb-8">
                    <div className="container">
                        <div className="row">
                            <SideBar />
                            <div className="col-md-8 col-xl-7 col-xxl-6 offset-xl-1">
                                <form onSubmit={submit} onChange={change} className="mb-4 mb-sm-5">
                                    <h5 className="gray-1"> </h5>
                                    <div className="gray-1 mb-2"> </div>
                                    <input type="text" name="name" placeholder=", " className="mb-3" />
                                    <div className="gray-1 mb-2"></div>
                                    <select className="mb-3" name="region">
                                        <option value="" selected disabled className="gray-4"> </option>
                                        <option value=" 1"> 1</option>
                                        <option value=" 2"> 2</option>
                                        <option value=" 3"> 3</option>
                                    </select>
                                    <div className="gray-1 mb-2"></div>
                                    <input type="text" name="street" placeholder="" className="mb-3" />
                                    <div className="row row-cols-1 row-cols-sm-3 gx-2 gx-md-4">
                                        <div>
                                            <div className="gray-1 mb-2"></div>
                                            <input type="text" name="home" placeholder="" className="mb-4" />
                                        </div>
                                        <div>
                                            <div className="gray-1 mb-2"></div>
                                            <input type="text" name="floor" placeholder="" className="mb-4" />
                                        </div>
                                        <div>
                                            <div className="gray-1 mb-2"></div>
                                            <input type="text" name="apartment" placeholder="" className="mb-4" />
                                        </div>
                                    </div>
                                    <div className="row row-cols-2 gx-2 gx-md-4">
                                        <div>
                                            <div className="gray-1 mb-2"></div>
                                            <input type="text" name="entrance" placeholder="" className="mb-4" />
                                        </div>
                                        <div>
                                            <div className="gray-1 mb-2"> </div>
                                            <input type="text" name="code" placeholder=" " className="mb-4" />
                                        </div>
                                    </div>
                                    <button type="submit" className="btn btn-2"></button>
                                </form>
                                <Link to={PROFILE_ROUTE + '/address'} className="gray-3 d-flex align-items-center">
                                    <img src="/images/icons/chevron-left.svg" alt=" " className="me-1" />
                                     
                                </Link>
                            </div>
                        </div>
                    </div>
                </section>
            </main>
        )
    } else if (action == 'edit' && actionId) {
        const submit = async (e) => {
            try {
                e.preventDefault();
                let data;
                data = await editAddress(address);
                if (data) {
                    user.setUser(data)
                }
            } catch (e) {
                if (e.response && e.response.data) {
                    alert(e.response.data.message)
                } else {
                    alert(e)
                }
            }
        }
        const change = (e) => {
            setAddress({ ...address, [e.target.name]: e.target.value })
        }
        return (
            <main>
                <div className="container mb-4 mb-md-5">
                    <nav aria-label="breadcrumb">
                        <ol className="breadcrumb mb-0">
                            <li className="breadcrumb-item"><Link to={HOME_ROUTE}></Link></li>
                            <li className="breadcrumb-item"><a> </a></li>
                        </ol>
                    </nav>
                </div>
                <section id="sec-13" className="mb-8">
                    <div className="container">
                        <div className="row">
                            <SideBar />
                            <div className="col-md-8 col-xl-7 col-xxl-6 offset-xl-1">
                                {(address) ?
                                    <form onSubmit={submit} onChange={change} className="mb-4 mb-sm-5">
                                        <h5 className="gray-1"> </h5>
                                        <div className="gray-1 mb-2"> </div>
                                        <input type="text" name="name" placeholder=", " className="mb-3" defaultValue={address.name} />
                                        <div className="gray-1 mb-2"></div>
                                        <select className="mb-3" name="region">
                                            <option value="" selected disabled className="gray-4"> </option>
                                            <option value=" 1"> 1</option>
                                            <option value=" 2"> 2</option>
                                            <option value=" 3"> 3</option>
                                        </select>
                                        <div className="gray-1 mb-2"></div>
                                        <input type="text" name="street" placeholder="" className="mb-3" defaultValue={address.street} />
                                        <div className="row row-cols-1 row-cols-sm-3 gx-2 gx-md-4">
                                            <div>
                                                <div className="gray-1 mb-2"></div>
                                                <input type="text" name="home" placeholder="" className="mb-4" defaultValue={address.home} />
                                            </div>
                                            <div>
                                                <div className="gray-1 mb-2"></div>
                                                <input type="text" name="floor" placeholder="" className="mb-4" defaultValue={address.floor} />
                                            </div>
                                            <div>
                                                <div className="gray-1 mb-2"></div>
                                                <input type="text" name="apartment" placeholder="" className="mb-4" defaultValue={address.apartment} />
                                            </div>
                                        </div>
                                        <div className="row row-cols-2 gx-2 gx-md-4">
                                            <div>
                                                <div className="gray-1 mb-2"></div>
                                                <input type="text" name="entrance" placeholder="" className="mb-4" defaultValue={address.entrance} />
                                            </div>
                                            <div>
                                                <div className="gray-1 mb-2"> </div>
                                                <input type="text" name="code" placeholder=" " className="mb-4" defaultValue={address.code} />
                                            </div>
                                        </div>
                                        <button type="submit" className="btn btn-2"></button>
                                    </form>
                                    : <div>  </div>
                                }
                                <Link to={PROFILE_ROUTE + '/address'} className="gray-3 d-flex align-items-center">
                                    <img src="/images/icons/chevron-left.svg" alt=" " className="me-1" />
                                     
                                </Link>
                            </div>
                        </div>
                    </div>
                </section>
            </main>
        )
    } else {
        return (
            <main>
                <div className="container mb-4 mb-md-5">
                    <nav aria-label="breadcrumb">
                        <ol className="breadcrumb mb-0">
                            <li className="breadcrumb-item"><Link to={HOME_ROUTE}></Link></li>
                            <li className="breadcrumb-item"><a> </a></li>
                        </ol>
                    </nav>
                </div>

                <section id="sec-13" className="mb-8">
                    <div className="container">
                        <div className="row">
                            <SideBar />
                            <div className="col-md-8 col-xl-7 col-xxl-6 offset-xl-1">
                                <form action="" className="mb-4 mb-sm-5">
                                    <h5 className="gray-1"> </h5>
                                    {
                                        (user.user.address && user.user.address.length > 0) ?
                                            <>
                                                <div className="fs-09 gray-1 mb-4">    .</div>
                                                {
                                                    user.user.address.map((item, i) =>
                                                        <div key={i} className="d-flex align-items-start mb-4">
                                                            <input type="radio" name="address" value=" " id={"address-" + i} defaultChecked={(i === 0) ?? true} />
                                                            <div className="ms-2">
                                                                <label for={"address-" + i} className="gray-1 fw-5">{item.name}</label>
                                                                <div className="d-flex mt-2">
                                                                    <Link to={PROFILE_ROUTE + '/address/edit/' + item.id} className="fs-09 gray-1 me-3"></Link>
                                                                    <button type="button" onClick={() => deleteSubmit(item.id)} className="fs-09 gray-4"></button>
                                                                </div>
                                                            </div>
                                                        </div>
                                                    )
                                                }
                                            </>
                                            : <div className="fs-09 gray-1 mb-4">     .</div>
                                    }
                                    <Link to={PROFILE_ROUTE + '/address/add'} className="d-flex align-items-center">
                                        <img src="/images/icons/plus3.svg" alt="" className="me-2" />
                                        <span className="primary"> </span>
                                    </Link>
                                </form>
                                <Link to={PROFILE_ROUTE} className="gray-3 d-flex align-items-center">
                                    <img src="/images/icons/chevron-left.svg" alt=" " className="me-1" />
                                     
                                </Link>
                            </div>
                        </div>
                    </div>
                </section>
            </main>
        )
    }
};

export default Address;
